<template>
  <div class="picture">
  <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
  </div>
    <van-row type="flex" justify="end">
      <h2><a :href="islogin" @click="judge" style="color:blue;">{{myid}}</a></h2>
  </van-row>

    <p><h1>{{ msg }}</h1></p>



    <div>
    <van-form @submit="onSubmit">

      <van-cell-group>
        <van-field
        v-model="message"
        rows="5"
        autosize
        label=""
        type="textarea"
        maxlength="150"
        placeholder=""
        show-word-limit/>
      </van-cell-group> 

      <div style="margin:16px">
        <van-uploader v-model="fileList" multiple :max-count="2" />
      </div>
        <div style="margin-top: 16px;">
        <van-button round  type="warning" native-type="submit" ></van-button>
        </div>
    </van-form>
  </div>


    

    <div>
    <p></p>
    :{{phone}} :{{email}} :{{weixin}}
    </div>


    <div style="margin-top:16px;">
        <Foot_bar></Foot_bar>
    </div>

  </div>
</template>

<script>
import Foot_bar from './Foot_bar'
export default {
  name: 'Renter',
  data () {
    return {
      islogin:'#/MyInfo',
      myid:localStorage.getItem('email'),
      msg: '',
      imgSrc:require('../assets/ConnectService.png'),
      fileList: [],
      value:'',
      message:'',
      phone:'17815566221',
      email:'1322496098@qq.com',
      weixin:'17815566221',
    };
  },
  components:{
      Foot_bar
  },
  methods:{
      judge(){
          if(this.myid==""){
              this.islogin="/";
          }
      },
      afterRead(file) {
          console.log(file);
      },
      onSubmit(values) {
          console.log('submit', values);
          this.$router.push({
              path:'/ConnectService',
          });
    },
  },
  created(){
      this.id=this.$route.query.name;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1{
  font-weight: normal;
}
.background{
    width:100%;  
    height:100%;  /**100% */
    z-index:-1;
    position: absolute;
}
 
.front{
    z-index:1;
    position: absolute;
}
</style>



